* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(29, 29, 29);
  font-size: 16px;
}
.form-registration {
  box-shadow: 2px solid rgb(65, 65, 65);
  border: 1px solid rgba(134, 134, 134, 0.295);
  width: 30vw;
  margin: 20vh auto 0 auto;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  padding-bottom: 5vh;
}

.title-form {
  text-align: center;
  font-size: 1.5em;
  padding: 5%;
}
.input-form {
  border: hidden;
  font-size: 1.5em;
  border-bottom: blue 1px solid;
  width: 100%;
  padding: 0 0 0 5%;
}
.input-form:focus {
  outline: none;
}
.enter {
  text-align: center;
  margin: auto;
  cursor: pointer;
  padding: 3%;
  background-color: rgb(98, 98, 248);
  color: white;
  font-size: 1.5em;
  border-radius: 15%;
  border-color: aliceblue;
  box-shadow: black 22px solid;
}
.enter:hover {
  font-size: 1.6em;
}
.account-is-exist {
  text-align: center;
  color: blue;
  cursor: pointer;
  margin-top: 5px;
  font-size: 1.3em;
}
.account-is-exist:hover {
  font-size: 1.4em;
}
@media (max-width: 650px) {
  .form-registration {
    font-size: 1em;
    width: 60vw;
  }
  .title-form {
    font-size: 1em;
  }
  .input-form {
    font-size: 1em;
  }
  .enter {
    font-size: 1em;
  }
  .enter:hover {
    font-size: 1.3em;
  }
  .account-is-exist {
    font-size: 1em;
  }
  .account-is-exist:hover {
    font-size: 1.3em;
  }
}
